import Posts from "./Posts";
import PostsContainer from "./PostsContainer";
import ProfileContent from "./Profile-contnet";
import Preloader from "../../common/Preloader";
import {setProfileStatus, updateProfileStatus} from "../../redux/posts-reducer";

const Profile = (props) => {

    if (!props.profile) {
        return <div>сасаить</div>
    }

    return (
        <div>
            {props.isLoading ? <Preloader/> : <ProfileContent profile={props.profile} status={props.status} updateProfileStatus={props.updateProfileStatus}/>}
            <PostsContainer/>
        </div>

    )
}

export default Profile;